import React, { useEffect, useRef, useState } from 'react';
import styles from './index.module.scss';

interface MinorProgressBarProps {
  progress: number;
}

const MinorProgressBar: React.FC<MinorProgressBarProps> = ({ progress }) => {
  const [charCount, setCharCount] = useState(50);
  const containerRef = useRef<HTMLDivElement>(null);
  const [stage1Progress, setStage1Progress] = useState(0);
  const [stage2Progress, setStage2Progress] = useState(0);

  //     
  useEffect(() => {
    if (progress > 0) {
      //      (  50%)
      setStage1Progress(Math.min(progress * 20, 100) );
      setStage2Progress(Math.max(0, Math.min( progress * 20 - 100, 100)));
    };
  }, [progress]);

  useEffect(() => {
    if (containerRef.current) {
      setCharCount(Math.floor(containerRef.current.offsetWidth / 2));
    }
  }, []);
  return (
    <div className={styles.minorProgressBar} ref={containerRef}>

      <div className={styles.progressLayer} style={{ 
        clipPath: `inset(0 ${Math.max(0, 100 - stage1Progress)}% 0 0)`,
        left: '0px'
      }}>
        {Array.from({ length: charCount }, (_, i) => (
          <span key={`stage1-${i}`} className={styles.progressChar}>|</span>
        ))}
      </div>
      

      <div className={styles.progressLayer} style={{ 
        clipPath: `inset(0 ${Math.max(0, 100 - stage2Progress)}% 0 0)`,
        left: '1px'
      }}>
        {Array.from({ length: charCount }, (_, i) => (
          <span key={`stage2-${i}`} className={styles.progressChar}>|</span>
        ))}
      </div>
    </div>
  );
};

export default MinorProgressBar; 