import React, { useMemo, useState } from "react";
import styles from "./index.module.scss";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { selectUpgrades, buyUpgrade, selectResources, selectUpgradeMultipliers, selectMultipliers } from "../../app/gameSlice";
import "./shop.scss";
import { UPGRADES } from "../../Upgrades";
import CountUp from "../../components/CountUp";
import { COMMON, ECONOMY, FAITH, MANUAL, SCIENCE } from "../../app/constants";
import { calculateUpgradeCost, calculateEffectiveCPS, calculateTotalUpgradeCPS } from "../../app/utils";
import classNames from "classnames";

//      
const UpgradeBackground: React.FC<{ icon: string; level: number }> = ({ icon, level }) => {
  //     ,   
  const icons = Array.from({ length: Math.min(level, 100) }, (_, index) => (
    <span key={index} className="upgrade-icon-bg">{icon}</span>
  ));
  
  return <div className="upgrade-background">{icons}</div>;
};

//   
interface TooltipData {
  isVisible: boolean;
  upgradeId: string;
  position: { x: number; y: number };
}

//    
const UpgradeTooltip: React.FC<{ 
  tooltip: TooltipData; 
  upgrade: any; 
}> = ({ tooltip, upgrade }) => {
  if (!tooltip.isVisible || !upgrade) return null;

  //      
  const getUpgradeAction = (sphere: string) => {
    switch (sphere) {
      case MANUAL:
        return '  ';
      case COMMON:
        return '   ';
      case ECONOMY:
        return '    ';
      case SCIENCE:
        return '    ';
      case FAITH:
        return '    ';
      default:
        return ' ';
    }
  };

  //  CPS      upgrade.totalCPS

  return (
    <div 
      className="upgrade-tooltip"
      style={{ 
        left: `${tooltip.position.x}px`, 
        top: `${tooltip.position.y}px`,
        position: 'fixed',
        zIndex: 1000,
        backgroundColor: 'var(--gunmetal)',
        color: 'var(--alabaster)',
        padding: '12px',
        borderRadius: '8px',
        border: '1px solid var(--cinnabar)',
        maxWidth: '280px',
        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.5)',
        pointerEvents: 'none'
      }}
    >
      <div style={{ fontWeight: 'bold', marginBottom: '8px', fontSize: '1.1em' }}>
        {upgrade.icon} {upgrade.name}
      </div>
      
      <div style={{ marginBottom: '6px', color: 'var(--cinnabar)' }}>
        {getUpgradeAction(upgrade.sphere)}
      </div>
      
      {upgrade.level > 0 && (
        <div style={{ marginBottom: '6px' }}>
          <strong> :</strong> {upgrade.totalCPS.toFixed(1)}
          {upgrade.sphere === MANUAL ? '  ' : 
           upgrade.sphere === COMMON ? '/' : 
           upgrade.sphere + '/'}
        </div>
      )}
      
      <div style={{ marginBottom: '6px', color: 'var(--cinnabar)' }}>
        <strong>  :</strong> {upgrade.effectiveCPS.toFixed(1)}
        {upgrade.sphere === MANUAL ? '  ' : 
         upgrade.sphere === COMMON ? '/' : 
         upgrade.sphere + '/'}
      </div>
      
      {upgrade.flavorText && (
        <div style={{ 
          fontStyle: 'italic', 
          color: 'var(--alabaster)', 
          opacity: 0.8,
          marginTop: '8px',
          fontSize: '0.9em'
        }}>
          "{upgrade.flavorText}"
        </div>
      )}
    </div>
  );
};

export const Shop = () => {
  const stateUpgrades = useAppSelector(selectUpgrades);
  const resources = useAppSelector(selectResources);
  const upgradeMultipliers = useAppSelector(selectUpgradeMultipliers);
  const multipliers = useAppSelector(selectMultipliers);
  const dispatch = useAppDispatch();
  
  //  
  const [tooltip, setTooltip] = useState<TooltipData>({
    isVisible: false,
    upgradeId: '',
    position: { x: 0, y: 0 }
  });
  
  //        
  const viewUpgrades = useMemo(() => {
    return UPGRADES.map(originalUpgrade => {
      const stateUpgrade = stateUpgrades.find(u => u.id === originalUpgrade.id);

      return {
        ...originalUpgrade,
        isVisible: stateUpgrade?.isVisible || false,
        level: stateUpgrade?.level || 0,
        cost: calculateUpgradeCost(stateUpgrade?.level || 0, {
          baseCost: originalUpgrade.cost,
          growthRate: originalUpgrade.growthRate,
          transitionLevel: originalUpgrade.transitionLevel,
          linearIncrement: originalUpgrade.linearIncrement,
        }),
        //   CPS   
        effectiveCPS: calculateEffectiveCPS(
          originalUpgrade.CPS,
          originalUpgrade.id,
          originalUpgrade.sphere,
          { multipliers, upgradeMultipliers }
        ),
        totalCPS: calculateTotalUpgradeCPS(
          originalUpgrade.CPS,
          stateUpgrade?.level || 0,
          originalUpgrade.id,
          originalUpgrade.sphere,
          { multipliers, upgradeMultipliers }
        )
      };
    }).filter(upgrade => upgrade.isVisible);
  }, [stateUpgrades, multipliers, upgradeMultipliers]);

  //     
  const handleMouseEnter = (upgradeId: string, event: React.MouseEvent) => {
    const rect = event.currentTarget.getBoundingClientRect();
    setTooltip({
      isVisible: true,
      upgradeId,
      position: {
        x: rect.right + 10,
        y: rect.top
      }
    });
  };

  const handleMouseLeave = () => {
    setTooltip(prev => ({ ...prev, isVisible: false }));
  };

  //    
  const tooltipUpgrade = viewUpgrades.find(u => u.id === tooltip.upgradeId);
  
  return (
    <div className={classNames(styles.shop)}>
      <div>
        {viewUpgrades
        .map(upgrade => (
          <div 
            key={upgrade.id}
            onMouseEnter={(e) => handleMouseEnter(upgrade.id, e)}
            onMouseLeave={handleMouseLeave}
          >
            <button 
              onClick={() => dispatch(buyUpgrade(upgrade.id))} 
              className={`upgrade-button ${resources < upgrade.cost ? 'disabled' : ''}`} 
              disabled={resources < upgrade.cost}
            >
            <UpgradeBackground icon={upgrade.icon} level={upgrade.level} />
            <div className="upgrade-content">
              <div className="upgrade-icon-main">{upgrade.icon}</div>
              <div className="upgrade-info">
                <div className="upgrade-name">
                  {upgrade.name}
                  <span className="upgrade-spheres">
                    {upgrade.sphere}
                  </span>
                  </div>
                <div className="upgrade-details">
                  <span className="upgrade-level">: {upgrade.level}</span>
                  <span className="upgrade-cost">
                    : <CountUp value={Math.round(upgrade.cost)} suffix="" duration={300} />
                  </span>
                  <span className="upgrade-gain">
                    : {upgrade.effectiveCPS.toFixed(1)}
                    
                  </span>
                </div>
              </div>
            </div>
          </button>
          </div>
        ))}
      </div>
      
      {/*    */}
      <UpgradeTooltip 
        tooltip={tooltip} 
        upgrade={tooltipUpgrade} 
      />
    </div>
  );
};
