import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from './store';
import { SoundState, PlaySoundPayload } from '../types/sound';
import { DEFAULT_SOUND_SETTINGS } from '../constants/sounds';

const initialState: SoundState = {
  ...DEFAULT_SOUND_SETTINGS,
  lastActivityTime: Date.now(),
  inactivityCount: 0,
  isAmbientPlaying: false,
};

export const soundSlice = createSlice({
  name: 'sound',
  initialState,
  reducers: {
    //  
    setMasterVolume: (state, action: PayloadAction<number>) => {
      state.masterVolume = Math.max(0, Math.min(1, action.payload));
    },
    
    setMusicVolume: (state, action: PayloadAction<number>) => {
      state.musicVolume = Math.max(0, Math.min(1, action.payload));
    },
    
    setEffectsVolume: (state, action: PayloadAction<number>) => {
      state.effectsVolume = Math.max(0, Math.min(1, action.payload));
    },
    
    //  
    toggleMute: (state) => {
      state.isMuted = !state.isMuted;
    },
    
    toggleMusicMute: (state) => {
      state.isMusicMuted = !state.isMusicMuted;
    },
    
    toggleEffectsMute: (state) => {
      state.isEffectsMuted = !state.isEffectsMuted;
    },
    
    setMute: (state, action: PayloadAction<boolean>) => {
      state.isMuted = action.payload;
    },
    
    //  
    updateActivity: (state) => {
      state.lastActivityTime = Date.now();
      
      if (state.isAmbientPlaying) {
        state.inactivityCount++;
        
        //    ,  
        if (state.inactivityCount >= DEFAULT_SOUND_SETTINGS.maxInactivityClicks) {
          state.inactivityCount = 0;
          state.isAmbientPlaying = false;
        }
      }
    },
    
    setAmbientPlaying: (state, action: PayloadAction<boolean>) => {
      state.isAmbientPlaying = action.payload;
      if (!action.payload) {
        state.inactivityCount = 0;
      }
    },
    
    //     ( SoundManager)
    playSound: (state, action: PayloadAction<PlaySoundPayload>) => {
      //     ,     SoundManager
      //     
      if (action.payload.key !== 'backgroundMusic' && action.payload.key !== 'ambientBackground') {
        state.lastActivityTime = Date.now();
        
        if (state.isAmbientPlaying) {
          state.inactivityCount++;
          
          if (state.inactivityCount >= DEFAULT_SOUND_SETTINGS.maxInactivityClicks) {
            state.inactivityCount = 0;
            state.isAmbientPlaying = false;
          }
        }
      }
    },
    
    //    
    resetSoundSettings: (state) => {
      Object.assign(state, {
        ...DEFAULT_SOUND_SETTINGS,
        lastActivityTime: Date.now(),
        inactivityCount: 0,
        isAmbientPlaying: false,
      });
    },
  },
});

export const {
  setMasterVolume,
  setMusicVolume,
  setEffectsVolume,
  toggleMute,
  toggleMusicMute,
  toggleEffectsMute,
  setMute,
  updateActivity,
  setAmbientPlaying,
  playSound,
  resetSoundSettings,
} = soundSlice.actions;

// 
export const selectSoundState = (state: RootState) => state.sound;
export const selectMasterVolume = (state: RootState) => state.sound.masterVolume;
export const selectMusicVolume = (state: RootState) => state.sound.musicVolume;
export const selectEffectsVolume = (state: RootState) => state.sound.effectsVolume;
export const selectIsMuted = (state: RootState) => state.sound.isMuted;
export const selectIsMusicMuted = (state: RootState) => state.sound.isMusicMuted;
export const selectIsEffectsMuted = (state: RootState) => state.sound.isEffectsMuted;
export const selectLastActivityTime = (state: RootState) => state.sound.lastActivityTime;
export const selectIsAmbientPlaying = (state: RootState) => state.sound.isAmbientPlaying;

export default soundSlice.reducer; 