import React, { useState, useEffect, useRef, useCallback } from 'react';
import {
  ComposableMap,
  Geographies,
  Geography,
  ZoomableGroup,
} from 'react-simple-maps';
import { feature } from 'topojson-client';
import { geoPath, geoNaturalEarth1 } from 'd3-geo';
import countriesData from '../../assets/pre/countries-110m.json';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { 
  selectCountry, 
  manualClick, 
  selectedCountry, 
  selectConqueredCountries, 
  selectManualClickPower,
  selectStage,
  selectShowMinistryPopup,
  selectTalentBeacons,
  collectTalent,
  selectFlags,
  placeFlagOnCountry,
  selectMapEvents,
  selectTotal,
  selectCommonMultiplier,
} from '../../app/gameSlice';
import { showCoinEffectThunk } from '../../app/visualEffectsSlice';
import { CoinEffects } from '../../components/CoinEffects';
import { MinistryPopup } from '../../components/MinistryPopup/MinistryPopup';
import { QuestSystem } from '../../components/QuestSystem/QuestSystem';
import { ParticleEffects, ParticleEffectsRef } from '../../components/ParticleEffects';
import { Feature } from 'geojson';
import styles from './WorldMap.module.css';
import { GameEvents } from '../../components/GameEvents';
import { Flag } from '../../types';
import { COUNTRIES } from '../../Countries';

interface Country {
  id: string;
  name: string;
  centroid: [number, number];
  color: 'default' | 'red' | 'yellow';
}



export const WorldMap: React.FC<{onCountryClick: (pos: { x: number; y: number }, countryId: string) => void}> = ({onCountryClick}) => {
  const dispatch = useAppDispatch();
  
  // Redux 
  const gameSelectedCountry = useAppSelector(selectedCountry);
  const conqueredCountries = useAppSelector(selectConqueredCountries);
  const manualClickPower = useAppSelector(selectManualClickPower);
  const stage = useAppSelector(selectStage);
  const showMinistryPopup = useAppSelector(selectShowMinistryPopup);
  const talentBeacons = useAppSelector(selectTalentBeacons);
  const flags = useAppSelector(selectFlags);
  const events = useAppSelector(selectMapEvents);
  const total = useAppSelector(selectTotal);
  const commonMultiplier = useAppSelector(selectCommonMultiplier)
  
  //    
  const [countries, setCountries] = useState<Country[]>([]);
  const [draggedFlag, setDraggedFlag] = useState<string | null>(null);
  const [animatingCountries, setAnimatingCountries] = useState<Set<string>>(new Set());
  const mapRef = useRef<SVGSVGElement>(null);
  const particleEffectsRef = useRef<ParticleEffectsRef>(null);
  
  //    TopoJSON    
  useEffect(() => {
    const topology = countriesData as any;
    const geographies = feature(topology, topology.objects.countries) as any;
    
    //         ComposableMap
    const projection = geoNaturalEarth1()
    .scale(160)
    .translate([window.innerWidth / 2, window.innerHeight / 2]);
    const path = geoPath().projection(projection);
    
    const countryList: Country[] = geographies.features.map((geo: any) => {
      const centroid = path.centroid(geo);
      return {
        id: geo.properties.name, //     ID  
        name: geo.properties.name,
        centroid: centroid as [number, number],
        color: 'default' as const,
      };
    });
    
    setCountries(countryList);
  }, []);

  const beacons = talentBeacons.map(b => {
    const centroid = countries.find(country=> country.id === b.countryId)?.centroid || [b.x, b.y];
    return {
      ...b,
      x: centroid[0],
      y: centroid[1]
    }
  });

  const handleCountryClick = useCallback((geo: Feature, event: React.MouseEvent) => {
    
    const countryId = geo.properties?.name as string;
    //      -   
    if (!gameSelectedCountry) {
      dispatch(selectCountry(geo.properties?.name || ''));
      setCountries(prev => 
        prev.map(c => c.id === countryId ? { ...c, color: 'red' } : c)
      );
      return;
    }
    
    // ,      
    const isClickableCountry = countryId === gameSelectedCountry || conqueredCountries.includes(countryId);
    
    if (isClickableCountry) {
      onCountryClick({ x: event.clientX, y: event.clientY}, countryId);      
      //   
      setAnimatingCountries(prev => new Set(Array.from(prev).concat(countryId)));
      setTimeout(() => {
        setAnimatingCountries(prev => {
          const newSet = new Set(prev);
          newSet.delete(countryId);
          return newSet;
        });
      }, 300);
    }
  }, [dispatch, gameSelectedCountry, conqueredCountries, manualClickPower, countries]);
  
  const handleFlagDragStart = (flagId: string, event: React.DragEvent) => {
    setDraggedFlag(flagId);
    event.dataTransfer.setData('text/plain', flagId);
  };
  
  const handleFlagDrop = (countryName: string, event: React.DragEvent) => {
    event.preventDefault();
    if (draggedFlag) {
      const country = countries.find(c => c.name === countryName);
      if (country) {
        //      Redux
        dispatch(placeFlagOnCountry({
          flagId: draggedFlag,
          countryId: countryName,
          x: country.centroid[0],
          y: country.centroid[1]
        }));
        
        //     ( )
        setCountries(prev => 
          prev.map(c => c.name === countryName ? { ...c, color: 'yellow' } : c)
        );
      }
      setDraggedFlag(null);
    }
  };
  
  const handleTalentBeaconClick = (beaconId: string) => {
    dispatch(collectTalent(beaconId));
  };
  
  const getCountryColor = useCallback((countryId: string): string => {
    // ,     
    if (countryId === gameSelectedCountry) {
      return '#ff0000'; //    
    }
    
    // ,     
    if (conqueredCountries.includes(countryId)) {
      return '#ffff00'; //    
    }
    
    //     
    return '#e0e0e0';
  }, [gameSelectedCountry, conqueredCountries]);

  const handleFilter = (event: any) => {
    return event.type !== "dblclick";
  };

  //    autoPower   
  useEffect(() => {    
    conqueredCountries.forEach((countryName) => {
      const country = countries.find(c => c.name === countryName || c.id === countryName);
      const countryData = COUNTRIES[countryName];
      
      if (country && countryData && countryData?.autoPower * commonMultiplier > 1) {
        //       (0-500)
        setTimeout(() => {
          dispatch(showCoinEffectThunk({
            position: {
              x: country.centroid[0],
              y: country.centroid[1], 
            },
            value: countryData.autoPower * commonMultiplier,
            prefix: '+',
            suffix: '', 
          }));
        }, Math.random() * 500);
      }
    });
  }, [conqueredCountries]);
  
  return (
    <div className={styles.worldMapContainer}>
    {/*   */}
    <ParticleEffects ref={particleEffectsRef} />
    
    {/*    */}
    {total > 100 && (
      <div className={styles.flagShelf}>
      {flags.filter(f => !f.countryId).map(flag => (
        <div
        key={flag.id}
        draggable
        onDragStart={(e) => handleFlagDragStart(flag.id, e)}
        className={styles.flag}
        >
          {/*  -    */}
          <svg width="30" height="40">
            <line x1="2" y1="0" x2="2" y2="40" stroke="#8B4513" strokeWidth="2" />
            <rect x="4" y="2" width="22" height="15" fill="#ff6b6b" stroke="#333" strokeWidth="1" />
          </svg>
        </div>
      ))}
      </div>
    )}
    
    {/*   */}
    <ComposableMap
    ref={mapRef}
    projection="geoNaturalEarth1"
    projectionConfig={{
      scale: 160,
      center: [0, 0],
    }}
    width={window.innerWidth}
    height={window.innerHeight}
    className={styles.map}
    onDoubleClick={(event: React.MouseEvent) => event.preventDefault()}
    >
    <ZoomableGroup
      {...({ filterZoomEvent: handleFilter } as any)}
    >
      <Geographies geography={countriesData}>
        {({ geographies }: { geographies: any[] }) =>
          geographies.map((geo: any) => (
          <Geography
            key={geo.rsmKey}
            geography={geo}
            {...({ onClick: (event: React.MouseEvent) => handleCountryClick(geo, event) } as any)}
            onDoubleClick={(event: React.MouseEvent) => event.preventDefault()}
            onDrop={(event: React.DragEvent) => handleFlagDrop(geo.properties?.name || '', event)}
            onDragOver={(event: React.DragEvent) => event.preventDefault()}
            fill={getCountryColor(geo.properties?.name || '')}
            stroke="#fff"
            strokeWidth={0.5}
            className={animatingCountries.has(geo.id) ? styles.countryAnimated : styles.countryDefault}
            style={{
              default: {
                outline: 'none',
                cursor: !gameSelectedCountry || (geo.properties?.name === gameSelectedCountry || conqueredCountries.includes(geo.properties?.name)) ? 'pointer' : 'default',
              },
              hover: {
                outline: 'none',
                fill: !gameSelectedCountry ? '#ccc' : getCountryColor(geo.id),
                  filter: (geo.properties?.name === gameSelectedCountry || conqueredCountries.includes(geo.properties?.name)) ? 'brightness(1.2)' : 'none',
              },
              pressed: {
                outline: 'none',
                transform: 'scale(0.95)',
              },
            }}
          />
        ))
      }
      </Geographies>
      
      {/*   */}
      {flags.filter(f => f.countryId).map(flag => (
        <g key={flag.id}>
          <line x1={flag.x} y1={flag.y} x2={flag.x} y2={flag.y + 25} stroke="#8B4513" strokeWidth="2" />
          <rect x={flag.x + 2} y={flag.y} width="15" height="10" fill="#ff6b6b" stroke="#333" strokeWidth="1" />
        </g>
      ))}
      
      {/*   */}
      {beacons.map(beacon => (
        <g key={beacon.id}>
          <circle
          cx={beacon.x}
          cy={beacon.y}
          r="8"
          fill="#ff4444"
          stroke="#fff"
          strokeWidth="2"
          className={styles.talentBeacon}
          onClick={() => handleTalentBeaconClick(beacon.id)}
          />
          <circle
          cx={beacon.x}
          cy={beacon.y}
          r="12"
          fill="none"
          stroke="#ff4444"
          strokeWidth="2"
          opacity="0.6"
          >
            <animate
            attributeName="r"
            values="12;20;12"
            dur="2s"
            repeatCount="indefinite"
            />
            <animate
            attributeName="opacity"
            values="0.6;0.2;0.6"
            dur="2s"
            repeatCount="indefinite"
            />
          </circle>
        </g>
      ))}

    </ZoomableGroup>
    </ComposableMap>
    
    {/*   */}
    <CoinEffects />
    
    {/*    */}
    {showMinistryPopup && <MinistryPopup />}
    
    {/*   */}
    {stage > 0 && <QuestSystem />}

    {/*      */}
    <GameEvents />
  </div>
);
};
