import React from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { selectShip, toggleShield } from '../../app/gameSlice';
import { SCIENCE, FAITH, ECONOMY } from '../../app/constants';
import styles from './ShieldControls.module.scss';

const sphereNames = {
  [SCIENCE]: ' ',
  [FAITH]: ' ',
  [ECONOMY]: ' '
};

const ShieldControls: React.FC = () => {
  const dispatch = useAppDispatch();
  const ship = useAppSelector(selectShip);
  
  const handleToggleShield = (sphere: string) => {
    dispatch(toggleShield({ sphere, active: !ship.activeShields[sphere] }));
  };
  
  return (
    <div className={styles.shieldControls}>
      
    </div>
  );
};

export default ShieldControls; 