import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { updateActivity } from '../app/soundSlice';
import { useSound } from '../components/SoundManager';
import { selectGame } from '../app/gameSlice';

export const useSoundIntegration = () => {
  const dispatch = useAppDispatch();
  const playSound = useSound();
  const game = useAppSelector(selectGame);
  
  //   
  // useEffect(() => {
  //   const handleUserActivity = () => {
  //     dispatch(updateActivity());
  //   };

  //   //   
  //   const events = ['click', 'keydown', 'mousemove', 'scroll', 'touchstart'];
    
  //   events.forEach(event => {
  //     document.addEventListener(event, handleUserActivity, { passive: true });
  //   });

  //   return () => {
  //     events.forEach(event => {
  //       document.removeEventListener(event, handleUserActivity);
  //     });
  //   };
  // }, []);

  //  
  useEffect(() => {
    if (game.gameOver) {
      playSound('gameOver');
    }
  }, [game.gameOver, playSound]);

  return {
    playClickSound: (type: 'country' | 'planet' = 'country') => {
      playSound(type === 'country' ? 'clickCountry' : 'clickPlanet');
    },
    playTalentSound: () => {
      playSound('talentActivation');
    },
    playProjectCompleteSound: () => {
      playSound('projectComplete');
    },
    playButtonSound: () => {
      playSound('buttonClick');
    }
  };
}; 