import React from 'react';
import { useDamageWave } from '../../../../hooks/useDamageWave';
import styles from './DangerStrip.module.scss';

interface DangerStripProps {}

export const DangerStrip: React.FC<DangerStripProps> = () => {
  const damageWave = useDamageWave();

  if (!damageWave.isActive) {
    return null;
  }

  //   
  const timeRemaining = Math.ceil(damageWave.timeRemaining / 1000);
  const minutes = Math.floor(timeRemaining / 60);
  const seconds = timeRemaining % 60;
  const timeText = `${minutes}:${seconds.toString().padStart(2, '0')}`;

  return (
    <div className={styles.dangerStrip}>
      <div className={styles.content}>
        <div className={styles.scrollingText}>
          <span className={styles.message}>
             {damageWave.message} 
          </span>
          <span className={styles.multiplier}>
             x{damageWave.multiplier}
          </span>
          <span className={styles.timeRemaining}>
            : {timeText}
          </span>
          {/*      */}
          <span className={styles.message}>
             {damageWave.message} 
          </span>
          <span className={styles.multiplier}>
             x{damageWave.multiplier}
          </span>
          <span className={styles.timeRemaining}>
            : {timeText}
          </span>
        </div>
      </div>
    </div>
  );
}; 