import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from "./store";
import {MANUAL, COMMON, FAITH, SCIENCE, ECONOMY} from "./constants";
import { GAME_EVENTS } from '../Events';
import { TALENTS } from '../Talents';
import { Projects as BaseProjects } from "../Projects";
import { GameState } from '../types';
import { applyTalentEffect, calculateUpgradeCost } from './utils';
import { Projects as PROJECTS } from '../Projects';
import { UPGRADES } from '../Upgrades';
import { News } from '../News';
import { QUESTS, getAllQuests } from '../Quests';
import { COUNTRIES } from '../Countries';

const initialState: GameState = {
  upgrades: UPGRADES.map(upgrade => ({
    id: upgrade.id,
    isVisible: typeof upgrade.visible === 'boolean' ? upgrade.visible : false,
    level: 0,
    cost: upgrade.cost,
    CPS: upgrade.CPS,
    available: upgrade.available
  })),
  selectedCountry: undefined,
  countries: {},
  conqueredCountries: [], // Initially empty - player only has their selected country
  resources: 0,
  manualClickPower: 1,
  autoClickPower: 0,
  multipliers: {
    [COMMON]: 1.0,
    [ECONOMY]: 1.0,
    [FAITH]: 1.0,
    [SCIENCE]: 1.0,
    [MANUAL]: 1.0,
  },
  //      -    1.0
  upgradeMultipliers: UPGRADES.reduce((acc, upgrade) => {
    acc[upgrade.id] = 1.0;
    return acc;
  }, {} as Record<string, number>),
  events: Object.keys(GAME_EVENTS),
  activeEvents: [],  //     {id: string; position: number[]}
  eventHistory: {},
  projects: Object.keys(BaseProjects),
  activeProjects: [],
  problems: [],
  activeProblems: [],
  conquestSpeed: 1.0,
  projectSpeed: 1.0,
  secondStage: [],
  stage: 0,
  gameOver: false,
  lastConquest: {
    lastTriggered: Date.now(),
    country: ''
  },
  mapEvents: [],
  talents: TALENTS.map(talent => ({
    id: talent.id,
    isVisible: false,
    isAvailable: false,
    state: talent.state,
  })),
  seed: Math.random(),
  clickEffects: {
    [ECONOMY]: 0,
    [FAITH]: 0,
    [SCIENCE]: 0,
  },
  total: 0,
  news: Object.keys(News),
  newsQueue: [],
  activeNews: null,
  lastNewsTime: 0,
  cosmicCivilization: {
    FTL: false,
    conquest: false,
    planetShield: false,
    shipShield: {
      [SCIENCE]: false,
      [FAITH]: false, 
      [ECONOMY]: false
    },
  },
  ship: {

    flightStartTime: 0,
    activeShields: {                      //     
      [SCIENCE]: false,
      [FAITH]: false, 
      [ECONOMY]: false
    },
  },
  miniGamesState: {},
  quests: getAllQuests().map(quest => ({
    id: quest.id,
    progress: 0,
    maxProgress: quest.maxProgress,
    isCompleted: false,
    isVisible: false
  })),
  showMinistryPopup: false,
  urgentMessage: {
    isActive: false,
    startTime: 0,
    duration: 45000
  },
  deathTimer: {
    isActive: true,
    startTime: Date.now(),
    totalDuration: 20 * 60 * 1000, // 20   
    gameYears: 100, // 100  
    realStartDate: new Date(), //    
  },
  //    
  talentBeacons: [],
  totalTalents: 0,
  countryTalentState: {},

  //    
  talentPoints: 0,
  spentTalentPoints: 0,

  //     
  flags: [],
  activeConquests: [],
  availableFlags: 0,
  empirePerimeter: [],
  spheres: {
    [ECONOMY]: {
      talentPointsSpent: 0,
    },
    [SCIENCE]: {
      talentPointsSpent: 0
    },
    [FAITH]: {
      talentPointsSpent: 0
    }
  }
};


const gameSlice = createSlice({
  name: 'game',
  initialState,
  reducers: {
    buyUpgrade: (state, action: PayloadAction<string>) => {
      const upgradeId = action.payload;
      const stateUpgrade = state.upgrades.find(u => u.id === upgradeId);
      const originalUpgrade = UPGRADES.find(u => u.id === upgradeId);
      
      if (stateUpgrade && originalUpgrade && state.resources >= stateUpgrade.cost) {
        //   
        const cost = calculateUpgradeCost(stateUpgrade.level, {
          baseCost: originalUpgrade.cost,
          growthRate: originalUpgrade.growthRate,
          transitionLevel: originalUpgrade.transitionLevel,
          linearIncrement: originalUpgrade.linearIncrement,
        });

        state.resources -= cost;
        
        //    
        stateUpgrade.level += 1;
        
        //       
        if (!state.upgradeMultipliers[upgradeId]) {
          state.upgradeMultipliers[upgradeId] = 1.0;
        }
        
        //   manualClickPower  autoClickPower   
        //    gameLoop.ts,     
      }
    },
    selectCountry: (state, action: PayloadAction<string>) => {
      if (!state.selectedCountry) {
        state.selectedCountry = action.payload;
        // When selecting a country, it's automatically conquered
        state.conqueredCountries = [action.payload];
        // We will base stage difference on fact of country selection
        state.stage = 1;
        //    
        state.showMinistryPopup = true;
        // add neighbors to perimeter
        state.empirePerimeter = COUNTRIES[action.payload].neighbours;
        
        //      
        const countryData = COUNTRIES[action.payload];
        if (countryData) {
          const startingFlags = countryData.startingFlags || 1;
          state.availableFlags = startingFlags;
          
          //    
          for (let i = 0; i < startingFlags; i++) {
            state.flags.push({
              id: `flag-${Date.now()}-${i}`,
              x: 0,
              y: 0,
              countryId: undefined
            });
          }
        }
      }
    },
    manualClick: (state, action: PayloadAction<number | undefined>) => {
      //     manual    
      let totalManualPower = 1; //   
      
      //    manual 
      state.upgrades.forEach(stateUpgrade => {
        const originalUpgrade = UPGRADES.find(u => u.id === stateUpgrade.id);
        if (originalUpgrade && originalUpgrade.sphere === MANUAL && stateUpgrade.level > 0) {
          const effectiveCPS = originalUpgrade.CPS * 
            (state.multipliers[MANUAL] || 1.0) * 
            (state.upgradeMultipliers[originalUpgrade.id] || 1.0);
          totalManualPower += effectiveCPS * stateUpgrade.level;
        }
      });
      
      const clickPower = (totalManualPower + (action.payload ?? 0)) * state.multipliers[MANUAL];

      state.resources += clickPower;
      state.total += clickPower;
    },
    updateResources: (state, action: PayloadAction<{
      delta: number;
      resources: {[entityType: string]: number;};
    }>) => {
      Object.keys(action.payload.resources).forEach(sphere => {
        if( sphere === COMMON) {
          state.resources += action.payload.resources[COMMON];
          state.total += action.payload.resources[COMMON];
        } else {
          // @ts-ignore
          state.spheres[sphere].resources += action.payload.resources[sphere];
          state.total += action.payload.resources[sphere];
        }
      });
    },
   
    activateTalent: (state, action: PayloadAction<string>) => {
      const talentId = action.payload;
      const talent = TALENTS.find(t => t.id === talentId);
      if (talent) {
        applyTalentEffect(talent.effects, state);
        //talent spends talent points
        state.spentTalentPoints += talent.cost;
        state.talentPoints -= talent.cost;
        const t = state.talents.find(t => t.id === talentId);
        if(t) {
          t.state = 'active';
          //     
          if( talent.news ) {
            state.newsQueue.push(talent.news);
          }
        }
        if ([ECONOMY,SCIENCE,FAITH].includes(talent.category as any)){
          state.spheres[talent.category].talentPointsSpent += talent.cost;
        }
      }
    },
    activateLaterTalentEffect: (state, action: PayloadAction<string>) => {
      const talentId = action.payload;
      const talent = TALENTS.find(t => t.id === talentId);
      if(talent) {
        if(talent.laterEffects) {
          Object.keys(talent.laterEffects).forEach(delay => {
            // @ts-ignore
              applyTalentEffect(talent.laterEffects[delay], state);
          });
        }
      }
    },
    // reducer to conquer a country
    conquerCountry: (state, action: PayloadAction<string | string[]>) => {
      const countryIds = Array.isArray(action.payload) ? action.payload : [action.payload];
      // Filter out countries that are already conquered
      const newConquests = countryIds.filter(id => !state.conqueredCountries.includes(id));
      
      if (newConquests.length > 0) {
        // Add newly conquered countries to the list
        state.conqueredCountries = [...state.conqueredCountries, ...newConquests];

        if(state.conqueredCountries.length === 2) {// first conquest
          //     
          state.newsQueue.push('special-conquest');
        }
      }
      state.mapEvents = state.mapEvents.filter(e => !newConquests.includes(e.targetCountry));
      //     
      state.resources += state.conqueredCountries.length * state.multipliers[COMMON] * 10 ;
    },
    //     
    setGameOver: (state, action: PayloadAction<string>) => {
      state.gameOver = true;
      state.gameOverReason = action.payload;
    },
    failProject: (state, action: PayloadAction<string>) => {
      const project = state.activeProjects.find(p => p.id === action.payload);
      if (project) {
        state.activeProjects = state.activeProjects.filter(p => p.id !== action.payload);
        PROJECTS[action.payload]?.failEffects?.forEach(effect => {
          effect(state);
        });
      }
    },
    completeProject: (state, action: PayloadAction<string>) => {
      const project = state.activeProjects.find(p => p.id === action.payload);
      if (project) {
        //    ,      
        project.status = 'success';
        //   
        PROJECTS[action.payload]?.effects?.forEach(effect => {
          effect(state);
        });
      }
    },
    //    
    resetGame: (state) => {
      return {
        ...initialState,
        stage: 0,
        gameOver: false,
        gameOverReason: undefined,
        activeEvents: [], //       
        activeProblems: [], //      
        news: Object.keys(News), //    
        newsQueue: [],
        activeNews: null,
        lastNewsTime: 0,
        urgentMessage: {
          isActive: false,
          startTime: 0,
          duration: 45000
        },
        deathTimer: {
          isActive: true,
          startTime: Date.now(),
          totalDuration: 20 * 60 * 1000,
          gameYears: 100,
          realStartDate: new Date(),
        },
        //     
        flags: [],
        activeConquests: [],
        availableFlags: 0
      };
    },
    updateGameState: (state, action: PayloadAction<Partial<GameState>>) => {
      return { ...state, ...action.payload };
    },
    resolveEvent: (state, action: PayloadAction<string>) => {
      const eventObj = state.activeEvents.find(e => e.id === action.payload);
      
      if (eventObj) {
        const eventId = eventObj.id;
        
        // increment event history
        if (state.eventHistory[eventId]) {
          state.eventHistory[eventId].count += 1;
          state.eventHistory[eventId].lastTriggered = Date.now();
        }
        
        // apply effects
        GAME_EVENTS[eventId].effects.forEach(effect => {
          effect(state);
        });
        
        // remove event from active events
        state.activeEvents = state.activeEvents.filter(e => e.id !== eventId);
        
        // remove event from events
        if (GAME_EVENTS[eventId].once) {
          state.events = state.events.filter(e => e !== eventId);
        }
      }
    },
    clickProblem: (state, action: PayloadAction<{problemId: string; projectId: string}>) => {
      const { problemId, projectId } = action.payload;
      const problemIndex = state.activeProblems.findIndex(p => p.id === problemId && p.projectId === projectId);
      
      if (problemIndex !== -1) {
        //    ,   ,     
        //    GameProblem 
      }
    },
    resolveProblem: (state, action: PayloadAction<{problemId: string; projectId: string}>) => {
      const { problemId, projectId } = action.payload;
      
      //    
      state.activeProblems = state.activeProblems.filter(
        p => !(p.id === problemId && p.projectId === projectId)
      );

      //     
      state.newsQueue.push('special-problem-solved');
    },
    failProblem: (state, action: PayloadAction<{problemId: string; projectId: string}>) => {
      const { problemId, projectId } = action.payload;
      
      //    
      state.activeProblems = state.activeProblems.filter(
        p => !(p.id === problemId && p.projectId === projectId)
      );
      
      //  failEffects  
      const project = PROJECTS[projectId];
      if (project && project.failEffects) {
        project.failEffects.forEach(effect => effect(state));
      }
      
      //    
      state.activeProjects = state.activeProjects.filter(p => p.id !== projectId);
    },
    showNews: (state, action: PayloadAction<string>) => {
      state.activeNews = action.payload;
      state.lastNewsTime = Date.now();
    },
    hideNews: (state) => {
      state.activeNews = null;
    },
    addToNewsQueue: (state, action: PayloadAction<string>) => {
      // ,     ID       
      if (state.news.includes(action.payload) && !state.newsQueue.includes(action.payload)) {
        state.newsQueue.push(action.payload);
      }
    },
    setStage: (state, action: PayloadAction<number>) => {
      state.stage = action.payload;
      //     ,   
      if (action.payload === 2) {
        state.urgentMessage = {
          isActive: false,
          startTime: 0,
          duration: 45000
        };
        state.secondStage = [...state.secondStage, 'space_travel_unlocked'];
      }
    },
    toggleShield: (state, action: PayloadAction<{sphere: string; active: boolean}>) => {
      const { sphere, active } = action.payload;
      if (sphere in state.ship.activeShields) {
        state.ship.activeShields[sphere] = active;
      }
    },
    setShieldsActive: (state, action: PayloadAction<{sphere: string; active: boolean}>) => {
      const { sphere, active } = action.payload;
      if (sphere in state.ship.activeShields) {
        state.ship.activeShields[sphere] = active;
      }
    },

    repairShip: (state, action: PayloadAction<void>) => {
      // Implementation of repairShip action
    },
    startShipFlight: (state, action: PayloadAction<void>) => {
      // Implementation of startShipFlight action
    },
    applyMiniGameEffect: (state, action: PayloadAction<{
      gameId: string;
      success: boolean;
      resourceType: 'global' | 'economy' | 'science' | 'faith' | 'shield';
      multiplier: number;
      duration: number;
    }>) => {
      const { gameId, success, resourceType, multiplier, duration } = action.payload;
      
      //  lastUsed  -  
      if (!state.miniGamesState) {
        state.miniGamesState = {};
      }
      
      //     
      state.miniGamesState[gameId] = {
        lastUsed: Date.now(),
        success,
        effectEndTime: Date.now() + (duration * 1000)
      };
      
      //     
      if (resourceType === 'global') {
        //  global     
        state.multipliers[ECONOMY] *= multiplier;
        state.multipliers[SCIENCE] *= multiplier;
        state.multipliers[FAITH] *= multiplier;
      } else {
        //   
        state.multipliers[resourceType] *= multiplier;
      }
    },
    showMinistryPopup: (state) => {
      state.showMinistryPopup = true;
    },
    hideMinistryPopup: (state) => {
      state.showMinistryPopup = false;
    },
    updateQuests: (state) => {
      //    
      state.quests.forEach(questState => {
        const questDefinition = QUESTS[questState.id];
        if (!questDefinition) return;
        
        //  
        questState.isVisible = questDefinition.checkVisibility(state);
        
        //    ,   
        if (questState.isCompleted) return;
        
        //  
        questState.progress = questDefinition.checkProgress(state);
        
        //  
        if (questDefinition.checkCompletion(state)) {
          questState.isCompleted = true;
          questState.completedAt = Date.now();
        }
      });
    },
    completeQuest: (state, action: PayloadAction<string>) => {
      const questId = action.payload;
      const quest = state.quests.find(q => q.id === questId);
      if (quest && !quest.isCompleted) {
        quest.isCompleted = true;
        quest.completedAt = Date.now();
        quest.progress = quest.maxProgress;
      }
    },
    //    
    addTalentBeacon: (state, action: PayloadAction<{
      countryId: string;
      x: number;
      y: number;
    }>) => {
      const { countryId, x, y } = action.payload;
      //  4   
      if (state.talentBeacons.length < 4) {
        const newBeacon = {
          id: `talent-beacon-${Date.now()}-${Math.random()}`,
          countryId,
          x,
          y,
          createdAt: Date.now()
        };
        state.talentBeacons.push(newBeacon);
      }
    },
    collectTalent: (state, action: PayloadAction<string>) => {
      const beaconId = action.payload;
      //  
      state.talentBeacons = state.talentBeacons.filter(beacon => beacon.id !== beaconId);
      //    ( 50)
      if (state.totalTalents < 50) {
        state.talentPoints += 1;
        state.totalTalents += 1
      }
    },
    updateCountryTalentState: (state, action: PayloadAction<{
      countryName: string;
      maxTalents: number;
      talentCD: number;
    }>) => {
      const { countryName, maxTalents, talentCD } = action.payload;
      if (!state.countryTalentState[countryName]) {
        state.countryTalentState[countryName] = {
          talentsGenerated: 0,
          maxTalents,
          lastTalentGenerated: 0,
          talentCD
        };
      } else {
        state.countryTalentState[countryName].maxTalents = maxTalents;
        state.countryTalentState[countryName].talentCD = talentCD;
      }
    },
    //    
    placeFlagOnCountry: (state, action: PayloadAction<{
      flagId: string;
      countryId: string;
      x: number;
      y: number;
    }>) => {
      const { flagId, countryId, x, y } = action.payload;
      const flag = state.flags.find(f => f.id === flagId);
      const countryData = COUNTRIES[countryId];
      
      if (flag && countryData && !flag.countryId) {
        //    
        flag.countryId = countryId;
        flag.x = x;
        flag.y = y;
        state.availableFlags -= 1;
        
        //   
        const conquestDuration = countryData.conquestTime || 10000; // 10   
        state.activeConquests.push({
          id: `conquest-${Date.now()}-${flagId}`,
          countryId,
          flagId,
          startTime: Date.now(),
          duration: conquestDuration
        });
      }
    },
    removeFlagFromCountry: (state, action: PayloadAction<string>) => {
      const flagId = action.payload;
      const flag = state.flags.find(f => f.id === flagId);
      
      if (flag && flag.countryId) {
        //    
        flag.countryId = undefined;
        flag.x = 0;
        flag.y = 0;
        state.availableFlags += 1;
        
        //   
        state.activeConquests = state.activeConquests.filter(c => c.flagId !== flagId);
      }
    },
    completeConquest: (state, action: PayloadAction<string>) => {
      const conquestId = action.payload;
      const conquest = state.activeConquests.find(c => c.id === conquestId);
      
      if (conquest) {
        const countryData = COUNTRIES[conquest.countryId];
        
        //  
        if (!state.conqueredCountries.includes(conquest.countryId)) {
          state.conqueredCountries.push(conquest.countryId);

          // 
          state.empirePerimeter = [
            ...state.empirePerimeter.filter(c => c !== conquest.countryId),//filter out that contry for caution
            ...countryData.neighbours.filter(c => !state.empirePerimeter.includes(c)) // perimeter around new country, but filter overlaps
          ]
          
          //    
          state.resources += countryData?.autoPower ? countryData.autoPower * 100 : 100;
          
          //    ,  
          if (countryData?.conquestBonus) {
            countryData.conquestBonus(state);
          }
        }
        
        //   
        state.activeConquests = state.activeConquests.filter(c => c.id !== conquestId);
        
        //   (  )
        const flag = state.flags.find(f => f.id === conquest.flagId);
        if (flag) {
          state.flags = state.flags.filter(f => f.id !== conquest.flagId);
        }
      }
    },
    //      
    setUpgradeMultiplier: (state, action: PayloadAction<{ upgradeId: string; multiplier: number }>) => {
      const { upgradeId, multiplier } = action.payload;
      state.upgradeMultipliers[upgradeId] = multiplier;
    },
    //     
    setSphereMultiplier: (state, action: PayloadAction<{ sphere: string; multiplier: number }>) => {
      const { sphere, multiplier } = action.payload;
      state.multipliers[sphere] = multiplier;
    },
  }
});


