export const { 
  manualClick, 
  buyUpgrade, 
  selectCountry, 
  updateResources,
  activateTalent,
  conquerCountry,
  setGameOver,
  resetGame,
  updateGameState,
  failProject,
  completeProject,
  resolveEvent,
  clickProblem,
  resolveProblem,
  failProblem,
  showNews,
  hideNews,
  addToNewsQueue,
  setStage,
  toggleShield,
  setShieldsActive,
  repairShip,
  startShipFlight,
  applyMiniGameEffect,
  showMinistryPopup,
  hideMinistryPopup,
  updateQuests,
  completeQuest,
  activateLaterTalentEffect,
  addTalentBeacon,
  collectTalent,
  updateCountryTalentState,
  placeFlagOnCountry,
  removeFlagFromCountry,
  completeConquest,
  setUpgradeMultiplier,
  setSphereMultiplier
} = gameSlice.actions;

export default gameSlice.reducer;

/**
 *      :
 * 
 * 1.  :
 * 
 * ```tsx
 * import { useAppDispatch } from './app/hooks';
 * import { setGameOver } from './app/gameSlice';
 * 
 * const MyComponent = () => {
 *   const dispatch = useAppDispatch();
 *   
 *   const handleFailure = () => {
 *     dispatch(setGameOver(' '));
 *   };
 *   
 *   return <button onClick={handleFailure}></button>;
 * };
 * ```
 * 
 * 2.    :
 * 
 * -   `isCritical: true`  
 * -        
 * 
 * 3.     :
 * 
 * -     `setGameOver`  
 */

//        
export const calculateTick = (state: GameState) => {
  //  
  state.activeProjects.forEach(project => {
    if (project.progress >= 100 && project.status === 'in_progress') {
      //  ,    
      project.status = 'success';
      
      //     
      state.newsQueue.push('special-project-complete');
      
      //   
      const projectData = PROJECTS[project.id];
      if (projectData && projectData.effects) {
        projectData.effects.forEach(effect => effect(state));
      }
    }
  });
  
  //      ,       
};
