import React, { useEffect, useRef, useCallback } from 'react';
import { Howl } from 'howler';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { 
  selectSoundState, 
  setAmbientPlaying, 
  playSound as playSoundAction 
} from '../app/soundSlice';
import { SOUND_MAP } from '../constants/sounds';
import { SoundKey } from '../types/sound';

interface SoundInstance {
  howl: Howl;
  isPlaying: boolean;
  currentVariant?: number;
}

export const SoundManager: React.FC = () => {
  const dispatch = useAppDispatch();
  const soundState = useAppSelector(selectSoundState);
  
  //   
  const soundInstances = useRef<Map<string, SoundInstance>>(new Map());
  
  //  
  const backgroundMusicRef = useRef<Howl | null>(null);
  const ambientSoundRef = useRef<Howl | null>(null);
  
  //    
  const inactivityTimerRef = useRef<NodeJS.Timeout | null>(null);
  
  //       
  const getSoundPath = useCallback((key: SoundKey, variant?: number): string => {
    const soundConfig = SOUND_MAP[key];
    if (!soundConfig) return '';
    
    if (Array.isArray(soundConfig.path)) {
      if (variant !== undefined && variant < soundConfig.path.length) {
        return soundConfig.path[variant];
      }
      //  
      return soundConfig.path[Math.floor(Math.random() * soundConfig.path.length)];
    }
    
    return soundConfig.path;
  }, []);
  
  //   
  const calculateVolume = useCallback((category: 'music' | 'effect' | 'ambient', baseVolume: number = 1): number => {
    if (soundState.isMuted) return 0;
    
    let categoryVolume = 1;
    
    switch (category) {
      case 'music':
        if (soundState.isMusicMuted) return 0;
        categoryVolume = soundState.musicVolume;
        break;
      case 'effect':
        if (soundState.isEffectsMuted) return 0;
        categoryVolume = soundState.effectsVolume;
        break;
      case 'ambient':
        if (soundState.isMusicMuted) return 0; // ambient   
        categoryVolume = soundState.musicVolume;
        break;
    }
    
    return soundState.masterVolume * categoryVolume * baseVolume;
  }, [soundState]);
  
  //    Howl 
  const createHowl = useCallback((key: SoundKey, variant?: number): Howl => {
    const soundConfig = SOUND_MAP[key];
    const path = getSoundPath(key, variant);
    
    return new Howl({
      src: [path],
      loop: soundConfig.loop || false,
      volume: calculateVolume(soundConfig.category, soundConfig.volume),
    });
  }, [getSoundPath, calculateVolume]);
  
  //  
  const playSound = useCallback((key: SoundKey, variant?: number, customVolume?: number) => {
    const soundConfig = SOUND_MAP[key];
    if (!soundConfig) return;
    
    //     
    if (key === 'backgroundMusic') {
      try {
        if (backgroundMusicRef.current) {
          backgroundMusicRef.current.stop();
        }
      
        backgroundMusicRef.current = createHowl(key, variant);
        
        //  
        setTimeout(() => {
          backgroundMusicRef.current?.play();
          backgroundMusicRef.current?.volume(0);
          backgroundMusicRef.current?.fade(0, 0.05, 1000 * 60 * 1);
        }, 1000 * 60 * 1);
        setTimeout(() => {
          backgroundMusicRef.current?.fade(0.05, Math.min(0.1, calculateVolume('music', soundConfig.volume)), 1000 * 60 * 1);
        }, 1000 * 60 * 2);
        setTimeout(() => {
          backgroundMusicRef.current?.fade(0.1, calculateVolume('music', soundConfig.volume), 1000 * 60 * 1);
        }, 1000 * 60 * 3);


      } catch (error) {
        console.error('Error playing background music:', error);
      }
      
      return;
    }
    console.log(backgroundMusicRef.current?.volume())
    
    if (key === 'ambientBackground') {
      if (ambientSoundRef.current) {
        ambientSoundRef.current.stop();
      }
      
      ambientSoundRef.current = createHowl(key, variant);
      ambientSoundRef.current.play();
      ambientSoundRef.current.fade(0, calculateVolume('ambient', soundConfig.volume), soundState.fadeInDuration);
      
      return;
    }
    
    //   
    const instanceKey = `${key}_${variant || 0}`;
    let instance = soundInstances.current.get(instanceKey);
    
    if (!instance || !instance.isPlaying) {
      const howl = createHowl(key, variant);
      
      if (customVolume !== undefined) {
        howl.volume(calculateVolume(soundConfig.category, customVolume));
      }
      
      instance = {
        howl,
        isPlaying: true,
        currentVariant: variant
      };
      
      soundInstances.current.set(instanceKey, instance);
      
      howl.play();
      
      howl.on('end', () => {
        instance!.isPlaying = false;
      });
    }
  }, [createHowl, calculateVolume, soundState.fadeInDuration]);
  
  //    
  const updateAllVolumes = useCallback(() => {
    //  
    if (backgroundMusicRef.current) {
      const musicConfig = SOUND_MAP.backgroundMusic;
      backgroundMusicRef.current.volume(calculateVolume('music', musicConfig.volume));
    }
    
    // Ambient 
    if (ambientSoundRef.current) {
      const ambientConfig = SOUND_MAP.ambientBackground;
      let volume = calculateVolume('ambient', ambientConfig.volume);
      
      //   ambient  
      if (soundState.isAmbientPlaying && soundState.inactivityCount > 0) {
        volume *= (1 - soundState.ambientVolumeReduction * soundState.inactivityCount);
      }
      
      ambientSoundRef.current.volume(Math.max(0, volume));
    }
    
    //  
    soundInstances.current.forEach((instance, key) => {
      if (instance.isPlaying) {
        const soundKey = key.split('_')[0] as SoundKey;
        const soundConfig = SOUND_MAP[soundKey];
        if (soundConfig) {
          instance.howl.volume(calculateVolume(soundConfig.category, soundConfig.volume));
        }
      }
    });
  }, [calculateVolume, soundState.isAmbientPlaying, soundState.inactivityCount, soundState.ambientVolumeReduction]);
  
  //  
  const checkInactivity = useCallback(() => {
    const now = Date.now();
    const timeSinceLastActivity = now - soundState.lastActivityTime;
    
    if (timeSinceLastActivity >= soundState.ambientFadeThreshold && !soundState.isAmbientPlaying) {
      dispatch(setAmbientPlaying(true));
      playSound('ambientBackground');
    }
  }, [soundState.lastActivityTime, soundState.ambientFadeThreshold, soundState.isAmbientPlaying, dispatch, playSound]);
  
  //     
  useEffect(() => {
    const handlePlaySound = (event: CustomEvent) => {
      const { key, variant, volume } = event.detail;
      playSound(key, variant, volume);
    };
    
    window.addEventListener('playSound', handlePlaySound as EventListener);
    
    return () => {
      window.removeEventListener('playSound', handlePlaySound as EventListener);
    };
  }, [playSound]);
  
  //     
  useEffect(() => {
    updateAllVolumes();
  }, [updateAllVolumes]);
  
  //  
  useEffect(() => {
    if (inactivityTimerRef.current) {
      clearInterval(inactivityTimerRef.current);
    }
    
    inactivityTimerRef.current = setInterval(checkInactivity, 1000);
    
    return () => {
      if (inactivityTimerRef.current) {
        clearInterval(inactivityTimerRef.current);
      }
    };
  }, [checkInactivity]);
  
  //  ambient    
  useEffect(() => {
    if (!soundState.isAmbientPlaying && ambientSoundRef.current) {
      ambientSoundRef.current.fade(ambientSoundRef.current.volume(), 0, 1000);
      setTimeout(() => {
        if (ambientSoundRef.current) {
          ambientSoundRef.current.stop();
        }
      }, 1000);
    }
  }, [soundState.isAmbientPlaying]);
  
  //     
  useEffect(() => {
    if (!backgroundMusicRef.current && !soundState.isMuted && !soundState.isMusicMuted) {
      playSound('backgroundMusic');
    }
  }, []);
  
  //   
  useEffect(() => {
    return () => {
      if (backgroundMusicRef.current) {
        backgroundMusicRef.current.stop();
      }
      if (ambientSoundRef.current) {
        ambientSoundRef.current.stop();
      }
      soundInstances.current.forEach(instance => {
        instance.howl.stop();
      });
      soundInstances.current.clear();
      
      if (inactivityTimerRef.current) {
        clearInterval(inactivityTimerRef.current);
      }
    };
  }, []);
  
  return null; //    UI
};

//        
export const useSound = () => {
  const dispatch = useAppDispatch();
  
  return useCallback((key: SoundKey, variant?: number, volume?: number) => {
    dispatch(playSoundAction({ key, variant, volume }));
    
    //      SoundManager
    window.dispatchEvent(new CustomEvent('playSound', {
      detail: { key, variant, volume }
    }));
  }, [dispatch]);
}; 