import React, { useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { selectUrgentMessage, setStage } from '../../app/gameSlice';
import styles from './UrgentMessage.module.scss';

interface UrgentMessageProps {}

export const UrgentMessage: React.FC<UrgentMessageProps> = () => {
  const dispatch = useAppDispatch();
  const urgentMessage = useAppSelector(selectUrgentMessage);
  const [displayTime, setDisplayTime] = useState(0);

  //     
  useEffect(() => {
    if (!urgentMessage.isActive) return;

    const interval = setInterval(() => {
      const now = Date.now();
      const elapsed = now - urgentMessage.startTime;
      const remaining = Math.max(0, urgentMessage.duration - elapsed);
      setDisplayTime(remaining);
    }, 100); //   100  

    return () => clearInterval(interval);
  }, [urgentMessage.isActive, urgentMessage.startTime, urgentMessage.duration]);

  //       
  const handleTimerClick = () => {
    dispatch(setStage(2));
  };

  //     MM:SS:MS
  const formatTime = (milliseconds: number): string => {
    const totalSeconds = Math.floor(milliseconds / 1000);
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    const ms = Math.floor((milliseconds % 1000) / 10); //    
    
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}:${ms.toString().padStart(2, '0')}`;
  };

  if (!urgentMessage.isActive) {
    return null;
  }

  return (
    <div className={styles.urgentMessage}>
      <div className={styles.messageContent}>
        <div className={styles.title}>
            
        </div>
        <div 
          className={styles.timer} 
          onClick={handleTimerClick}
          title="   "
        >
          {formatTime(displayTime)}
        </div>
        <div className={styles.subtitle}>
            ,      
        </div>
      </div>
    </div>
  );
}; 